.container {
  height: 100%;
  width: 100%;
  background-color: #282c34;
  display: flex;
}

.sidebar {
  width: 15%;
  border-right: 1px solid #5c8d29;
}

@media screen and (max-device-width: 960px) {
  .sidebar {
    display: none;
  }
}

.sidebar-heading {
  display: flex;
  align-items: center;
  padding: 1rem;
  box-sizing: border-box;
  font-size: 1.2rem;
  color: #ffffff90;
  height: 5%;
}

.sidebar > .list {
  list-style-type: none;
  padding-inline-start: 0px;
}

.sidebar > .list > .list-item {
  color: #ffffff90;
  background-color: #5c8d29;
  padding: 1rem;
  font-size: 1.2rem;
  border-bottom: 1px solid #5c8d29;
}

.main-content {
  width: 80%;
}

@media screen and (max-device-width: 960px) {
  .main-content {
    width: 100%;
  }
}

.main-content-heading {
  display: flex;
  align-items: center;
  padding: 1rem;
  box-sizing: border-box;
  font-size: 2rem;
  color: #ffffff90;
  height: 9%;
  width: 100%;
}

@media screen and (max-device-width: 960px) {
  .main-content-heading {
    border-bottom: 1px solid #5c8d29;
  }
}

.main-content-body {
  display: flex;
  width: 100%;
  padding: 1rem;
}

.card {
  border: 1px solid #ffffff90;
  width: 25%;
  height: 20%;
  border-radius: 1rem;
}

@media screen and (max-device-width: 960px) {
  .card {
    width: 30%;
  }
}

@media screen and (max-device-width: 768px) {
  .card {
    width: 40%;
  }
}

@media screen and (max-device-width: 480px) {
  .card {
    width: 50%;
  }
}

.card-header {
  border-bottom: 1px solid #ffffff90;
  padding: 1rem;
  box-sizing: border-box;
  font-size: 1rem;
  font-weight: 700;
  color: #ffffff90;
}

.card-body {
  padding: 1rem;
  box-sizing: border-box;
  font-size: 1rem;
  color: #ffffff90;
}

.link {
  text-decoration: underline;
  color: #ffffff90;
}
